/* src/styles/Projects.css */
.projects {
    text-align: center;
    padding: 3rem 1rem;
  }
  
  .projects-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1.5rem;
    max-width: 1000px;
    margin: 0 auto;
  }
  
  .project-card {
    background: #f8f9fa;
    padding: 1.5rem;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    max-width: 300px;
  }
  
  .project-card h2 {
    font-size: 1.5rem;
    color: #333;
  }
  
  .project-card p {
    font-size: 1rem;
    color: #666;
    margin-bottom: 0.5rem;
  }
  
  .project-card a {
    text-decoration: none;
    color: #007bff;
    font-weight: bold;
  }
  
  .project-card a:hover {
    text-decoration: underline;
  }
  